import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { User } from './user';
import { Product } from './product';
@Injectable({
  providedIn: 'root'
})
export class RegistrationService {

  constructor(private http:HttpClient) { }

  public doRegistration(user: User) {
    return this.http.post("http://localhost:8080/create", user, {responseType: 'text' as 'json'});
  }
public addProductToCart(product : Product){
  return this.http.post("http://localhost:8080/addProduct",product, {responseType: 'text' as 'json'});
}
public showMyCart(){
  return this.http.get("http://localhost:8080/addProduct");
}
public deleteProd(imgU){
  return this.http.get(`http://localhost:8080/deleteProducts?imgUrl=${imgU}`);
}
public deleteALLProducts()
{
  return this.http.get("http://localhost:8080/deleteAllProducts");
}
public addToHistory(product)
{
  return this.http.post("http://localhost:8080/history", product, {responseType: 'text' as 'json'});
}
public getAllHistory()
{
  return this.http.get("http://localhost:8080/history");
}
public gettingCredentials()
{
  return this.http.get("http://localhost:8080/getAll");
}
public gettingLeeImages()
{
  return this.http.get("http://localhost:8080/topics");
}
public gettingKurtiImages()
{
  return this.http.get("http://localhost:8080/kurtis");
}
public gettingFloralshirtsImages()
{
  return this.http.get("http://localhost:8080/floralShirt");
}
public gettingDecorImages()
{
  return this.http.get("http://localhost:8080/decor");
}
}