import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SignupComponent } from './signup/signup.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { LeeComponent } from './lee/lee.component';
import { KurtisComponent } from './kurtis/kurtis.component';
import { FloralShirtsComponent } from './floral-shirts/floral-shirts.component';
import { DecorComponent } from './decor/decor.component';
import { Cart1Component } from './cart1/cart1.component';
import { HistoryComponent } from './history/history.component';


const routes: Routes = [
  {path:'signup', component:SignupComponent}, {path:'login',component:LoginComponent},
  {path:'home', component:HomeComponent}, {path:'lee', component:LeeComponent},
  {path:'kurti',component:KurtisComponent},{path:'floralshirt',component:FloralShirtsComponent},
  {path:'decor',component:DecorComponent}, {path:'cart',component:Cart1Component}, {path:'history', component:HistoryComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule { }
export const routingComponents=[SignupComponent,LoginComponent,HomeComponent,LeeComponent,KurtisComponent,FloralShirtsComponent,DecorComponent,Cart1Component,HistoryComponent]
